<template>
  <div class="home">
    <van-swipe :autoplay="3000" indicator-color="white" class="home-swipe">
      <van-swipe-item>1</van-swipe-item>
      <van-swipe-item>2</van-swipe-item>
      <van-swipe-item>3</van-swipe-item>
      <van-swipe-item>4</van-swipe-item>
    </van-swipe>

    <div class="tab-active">
      <van-tabs v-model="active">
        <van-tab>
          <div slot="title" class="tabs-icon">
            <img class="icon-box" src="../assets/chicken.png"/>散养土鸡
          </div>
        </van-tab>
        <van-tab>
          <div slot="title" class="tabs-icon">
            <img class="icon-box" src="../assets/egg.png"/>土鸡蛋
          </div>
        </van-tab>
      </van-tabs>
    </div>

    <div class="commodity-card">
        <ul>
          <li v-for="(item,index) in 3" @click="goDetails(index)">
            <div class="commodity-img"></div>
            <div class="commodity-introduce">
              <div class="commodity-left">
                <div class="commodity-name textOverFlow">
                  <span style="font-weight: bold">name</span>
                  <span style="color:red;">168<span style="color:#999">元/斤</span></span>
                </div>
                <div style="font-size:12px;color:#666;margin-top: 1px;">
                  这里是介绍 这里是介绍 这里是介绍
                </div>
              </div>
              <div class="card-shopping">
                <van-icon name="shopping-cart-o"  size="1.5rem"  color ="red" style="margin-top: 0.2rem;"/>
              </div>
            </div>
          </li>
        </ul>
    </div>
  </div>
</template>

<script>
  export default {
    data(){
      return {
        active:0,
        tabData:[
          {
            icon:'./assents/chicken.png',
            name:'散养土鸡',
          },
          {
            icon:'./assents/egg.png',
            name:'土鸡蛋',
          }
        ]
      }
    },
    methods:{
      goDetails(index){
        this.$router.push({path:'./details'})
      }
    }
  }
</script>

<style lang="scss" scoped>
    .home{
      background: #F1F1F1;
      .van-swipe{
        width: 100%;
        height: 12rem;
        background: antiquewhite;
        line-height: 12rem;
        text-align: center;
      }
      .icon-box{
        display: inline-block;
        width: 1.5rem;
        height: 1.5rem;
        margin-right: 0.5rem;
      }
      .tabs-icon{
        display: flex;
        align-items: center;
        justify-content: center;
      }
      .tab-active{
        margin: 0.3rem 0 0.3rem 0;
      }
      .commodity-card{
        width: 100%;
        background: #fff;
        padding: 0.6rem;
        box-sizing: border-box;
      }
      .commodity-introduce{
        color:#000;
        text-align: left;
        padding: 0.5rem  0.8rem;
        display: flex;
        box-sizing: border-box;
      }
      .commodity-img{
        height: 9rem;
        background: bisque;
        border-radius: 2%;
        box-sizing: border-box;
      }
      .commodity-card>ul>li{
        min-height: 12rem;
        box-shadow: 0 0 8px 0 #999, 0 2px 4px 0 rgba(232,237,250,.5);
        box-sizing: border-box;
        border-radius: 2%;
        margin-top: 1rem;
      }
      .card-shopping{
        width: 2rem;
        height: 2rem;
        border-radius: 50%;
        box-shadow: 0 0 8px 0 #999, 0 2px 4px 0 #666;
        text-align: center;
      }
      .commodity-left{
        flex: 1;
      }
    }
</style>




